<template>
  <div class="Appeals">
    <div class="header">
      <div class="wrapper">
        <h1 class="title">Оставить обращение</h1>
      </div>
    </div>

    <el-alert
      v-if="!current_user"
      :closable="false"
      type="warning"
      center
      show-icon
      class="notice"
      title="Только зарегистрированные пользователи могут оставлять обращения."
    />

    <div class="container">
      <h3 class="caption">1) Опишите проблему</h3>

      <el-input
        v-model="appeal.description"
        :rows="5"
        :disabled="!current_user"
        placeholder="О чём хотите сообщить?"
        type="textarea"
        autosize
        class="title"
      />

      <h3 class="caption">2) Какие решения Вы видите?</h3>
      <el-input
        v-model="appeal.proposal"
        :rows="5"
        :disabled="!current_user"
        type="textarea"
        autosize
        placeholder="Предложите свои решения проблемы"
        class="description"
      />

      <h3 class="caption">3) Прикрепите вложение</h3>
      <el-row
        v-loading="isSendingAttachment"
        class="file-container"
      >
        <label :class="['file', { disabled: !current_user }]">
          <input
            :disabled="!current_user"
            type="file"
            hidden
            @change="getAttachment"
          >
          Прикрепить
        </label>

        <div
          v-for="(name, index) in fileNames"
          :key="index"
          class="attached"
        >
          Прикреплённый файл: {{ name }}
        </div>
      </el-row>

      <el-button
        v-loading="isSendingAppeal"
        :disabled="!current_user || isSendingAttachment"
        type="primary"
        class="send"
        @click="sendAppeal"
      >
        Отправить
      </el-button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Appeals',

  metaInfo: {
    title: 'Обращение гражданина',
  },

  data() {
    return {
      appeal: {
        proposal: '',
        description: '',
        attachments: [],
      },

      fileNames: [],
      attachment: undefined,
      isSendingAttachment: false,

      isSendingAppeal: false,
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),
  },

  methods: {
    // На случай, если FileReader не справится: https://github.com/transloadit/uppy
    getAttachment({ target: { files: [file] } }) {
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);

      this.fileNames.push(file.name);

      reader.onload = async () => {
        this.attachment = reader.result;
        await this.sendAttachment();
      };

      reader.onerror = (err) => {
        console.error('Get Attachment Error:', err);
      };
    },

    async sendAttachment() {
      this.isSendingAttachment = true;

      const data = new FormData();
      data.append('file', new Blob([this.attachment]));

      try {
        const { data: response } = await this.$http({
          method: 'post',
          url: '/civil_appeals/attachment',
          config: { headers: { 'Content-Type': 'multipart/form-data' } },
          data,
        });

        this.$message.success('Вложение успешно загружено!');
        this.appeal.attachments.push(response);
      } catch (err) {
        this.$message.error('Вложение не загружено!');
        console.dir(err);
      } finally {
        this.isSendingAttachment = false;
      }
    },

    async sendAppeal() {
      this.isSendingAppeal = true;

      try {
        await this.$http.post('/civil_appeals', { civil_appeal: this.appeal });
        this.$message.success('Обращение успешно отправлено!');
        this.$router.push({ name: 'Surveys' });
      } catch (err) {
        this.$message.error('Обращение не отправлено!');
        console.dir(err);
      } finally {
        this.isSendingAppeal = false;
      }
    },
  },
};
</script>

